@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  min-height: 100vh;
  background-color: #2b3136;
}


body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #2b3136;
}

.zuri-head {
  border-bottom: 1px solid #595c62;
  padding: 5px; /* */
  padding-left: 15px;
}

.zuri-head h1 {
  text-align: left;
  font-size: 1em;
  color: #01a3a4;
}

.zuri-head #title::first-letter {
  font-size: 2em;
  color: #ff6b6b;
}

.zuri-body {
  border-top: 1px solid #595c62;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 3px;
  
}


.percentage-wrapper{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #b3cfcc;
  border-radius: 8px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.percentage-wrapper span{
  text-align: center;
  font-size: 2em;
  color: #2b3136;
}

.percentage-wrapper #score-body{
  font-size: 15em;
}
.percentage-wrapper #score-sign{
  font-size: 30px;
}

.percentage-wrapper label{
  font-size: 1.5em;
  text-transform: capitalize;
  text-align: center;
  color: #2b3136;
}

@media only screen and (max-width:500px){
  .percentage-wrapper #score-body{
    font-size: 8em;
  }

  .percentage-wrapper #score-sign{
    font-size: 20px;
  }
}
.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 5px;
  padding-top:30px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  min-height: 150px;
  max-width: 600px;
  width: 100%;
}

.form .error-message {
  min-height: 22px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 100%;
}

.form .error-message span {
  text-align: center;
  text-transform: lowercase;
  font-size: 0.9em;
  color: #ff8049;
}

.form input {
  font-size: 1.5em;
  width: 100%;
  text-align: center;
  background-color: transparent;
  border: 0px;
  border-bottom: 2px solid #52575b;
  color: #fff;
}

.form input:focus {
  outline: none;
}

.container-content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 60px auto;
}

